import { Entity, Column, PrimaryColumn } from 'typeorm'
import { ObjectType, Field, ID} from "type-graphql";

@Entity({ name:'TB_USER' })
@ObjectType()
export default class UserEntity {
	
	@PrimaryColumn()
	@Field(()=>ID)
	id?: string

	@Column()
	@Field(()=>String)
	email?: string

	constructor({
		id, email
	}:UserEntity){
		this.id = id
		this.email = email
	}
}

export { UserEntity }